<template>
	<div class="receive">
		<div class="myaddress_header">
			<i class="icon_arrow_left" @click="goBack"></i>
			<p class="myaddress_title">免费领取</p>
		</div>
		<p class="receive_tips">
			<i class="icon_tips"></i>
			<span>确认后信息不可修改，免费领商品均不可退换货</span>
		</p>
		<div class="receive_main" @click="selectAddress">
			<i class="icon_address"></i>
			<div class="receive_main_box" v-if="addressFlag">
				<p class="text1">
					<span>{{ addressObj.name }}</span>
					<span>{{ addressObj.tel }}</span>
				</p>
				<p class="text2">{{ addressObj.address }}</p>
			</div>
			<div class="receive_main_box" v-else>
				<p class="text2">请选择收货地址</p>
			</div>
			<i class="icon_arrow_right_weight"></i>
		</div>
		<ul class="goods_info">
			<li class="info_box">
				<img :src="receiveObj.commodityMainImg" alt="" class="img_info_box">
				<div class="info_box_main">
					<p class="text1">
						<i></i>
						<span>{{ receiveObj.commodityName }}</span>
					</p>
					<p class="text2">淘宝价:<span>￥{{ receiveObj.officialPrice }}</span></p>
					<p class="text3">数量×1</p>
				</div>
			</li>
			<li class="price_box">
				<p>
					<span>商品原价：</span>
					<span>￥{{ receiveObj.officialPrice }}</span>
				</p>
				<p>
					<span>平台减免：</span>
					<span>-￥{{ receiveObj.officialPrice }}</span>
				</p>
				<p>
					<span>邮费：</span>
					<span>￥0</span>
				</p>
			</li>
			<li class="price_box2">
				<p>
					<span>实际付款：</span>
					<span>￥0</span>
				</p>
			</li>
		</ul>
		<div class="receive_bottom">
			<p class="receive_bottom_text">到手价：<span>￥0</span></p>
			<img src="../../static/img/btn_qrlq.png" class="btn_qrlq" @click="prizeReceive"/>
		</div>
		<div class="lqcg" v-if="lqcgFlag">
			<img src="../../static/img/img_lqcg.png" @click="onLqcg"/>
		</div>
	</div>
</template>
<script>
	export default{
		name:'receive',
		data(){
			return{
				obj:{},
				receiveObj:{},
				addressObj:{},
				addressFlag:false,
				lqcgFlag:false
			}
		},
		mounted() {
			var jsonObj = localStorage.getItem("obj");
			var receiveObj = localStorage.getItem("receiveObj");
			var addressObj = localStorage.getItem("addressObj");
			console.log(addressObj);
			if(!!addressObj){
				this.addressFlag = true;
				this.addressObj = JSON.parse(addressObj);
			}
			this.obj = JSON.parse(jsonObj);
			this.receiveObj = JSON.parse(receiveObj);
		},
		methods:{
			goBack(){
			  this.$router.go(-1);
			},
			selectAddress(){
				let _this = this;
				_this.$router.push({path:'/myaddress',query:{type:0}});
			},
			prizeReceive(){
				let _this = this;
				if(_this.addressFlag){
					var exclusiveObj = {
						activityInfoId:_this.receiveObj.activityInfoId,
						addressId:_this.addressObj.id,
						goodsId:_this.receiveObj.goodsId
					};
					console.log(exclusiveObj);
					var requestData = Object.assign(_this.obj,exclusiveObj);
					var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
					_this.$axios({
						method: 'post',
						url: '/prize/receive',
						data:redata,
					}).then(function(res){
						if(res.data.code === 200){
							_this.lqcgFlag = true;
						}
					})
				}else{
					_this.$toast('请选择地址！')
				}
				
			},
			onLqcg(){
				this.$router.push({path:'/'})
			}
		}
	}
</script>

